import React, {Component} from 'react';
import {connect} from 'react-redux';

class Me extends Component {
  render() {
    const data = this.props.ReducerZonayed;
    if(!data) {
        return (
          <div className="card-content me">
            Data Loading...
          </div>
        )
    }

    return (
      <div className="card-content me">
        <h4>I am {data.data.details.fullName}</h4>
        <p>{data.data.details.description}</p>
      </div>
    )
  }
}

function mapStateToProps({ReducerZonayed}) {
  return {ReducerZonayed}
}

export default connect(mapStateToProps)(Me);



