import React, {Component} from 'react';
import {connect} from 'react-redux';

class Skills extends Component {
  render() {
    const data = this.props.ReducerZonayed;

    if(!data) {
        return (
          <div className="card-content me">
            Data Loading...
          </div>
        )
    }

    const pLan = data.data.skills.programmingln.map((s) => {
      return <li className="list-inline-item" key={s}>{s}</li>
    });

    const web = data.data.skills.web.map((s) => {
      return <li className="list-inline-item" key={s}>{s}</li>
    });

    const soft = data.data.skills.soft.map((s) => {
      return <li className="list-inline-item" key={s}>{s}</li>
    });

    const basic = data.data.skills.basic.map((s) => {
      return <li className="list-inline-item" key={s}>{s}</li>
    });

    const language = data.data.skills.language.map((s) => {
      return <li className="list-inline-item" key={s}>{s}</li>
    });

    return (
      <div className="card-content skills">
        <h4>Programming Language</h4>
        <ul className="list-inline">{pLan}</ul>
        <h4>Web Skills</h4>
        <ul className="list-inline">{web}</ul>
        <h4>Soft Skills</h4>
        <ul className="list-inline">{soft}</ul>
        <h4>Basic Skills</h4>
        <ul className="list-inline">{basic}</ul>
        <h4>Language</h4>
        <ul className="list-inline">{language}</ul>
      </div>
    )
  }
}

function mapStateToProps({ReducerZonayed}) {
  return {ReducerZonayed}
}

export default connect(mapStateToProps)(Skills);



