import React from 'react';

import Logo from '../logo.svg';

const NavItems = () => {
  return (
    <div className="logo-area">
      <div className="container">
        <div className="row">
          <img src={Logo} alt="logo"/>
        </div>
      </div>
    </div>
  )
}

export default NavItems;



