import React from 'react';

const Footer = () => {
  return (
    <footer className="footer-area">
      <div className="container">
        <div className="row">
          <ul className="list-inline footer">
            <li className="list-inline-item">React</li>
            <li className="list-inline-item">Redux</li>
            <li className="list-inline-item">React Router</li>
            <li className="list-inline-item">Axios</li>
            <li className="list-inline-item">Love</li>
          </ul>
        </div>
      </div>
    </footer>
  )
}

export default Footer;



