import React, {Component} from 'react';
import {connect} from 'react-redux';

class Web extends Component {
  render() {
    const data = this.props.ReducerZonayed;

    if(!data) {
        return (
          <div className="card-content me">
            Data Loading...
          </div>
        )
    }

    const webLinks = data.data.web.map((s) => {
      return <li className="list-inline-item" key={s.name}><a href={s.link}>{s.name}</a></li>
    });

    return (
      <div className="card-content skills">
        <h4>I am Available</h4>
        <ul className="list-inline">{webLinks}</ul>
      </div>
    )
  }
}

function mapStateToProps({ReducerZonayed}) {
  return {ReducerZonayed}
}

export default connect(mapStateToProps)(Web);



