import React from 'react';
import { Link } from "react-router-dom";

const NavItems = () => {
  return (
    <div className="nav-area">
      <div className="container">
        <div className="row">
          <ul className="list-inline">
            <li className="list-inline-item"><Link to="/">Me</Link></li>
            <li className="list-inline-item"><Link to="/skills">Skills</Link></li>
            <li className="list-inline-item"><Link to="/work">Work</Link></li>
            <li className="list-inline-item"><Link to="/social">Social</Link></li>
            <li className="list-inline-item"><Link to="/web">Web</Link></li>
          </ul>
        </div>
      </div>
    </div>
  )
}

export default NavItems;



