import React, {Component} from 'react';
import {connect} from 'react-redux';

class Work extends Component {
  render() {
    const data = this.props.ReducerZonayed;

    if(!data) {
        return (
          <div className="card-content me">
            Data Loading...
          </div>
        )
    }

    const webapp = data.data.portfolio.webapplication.map((s) => {
      return <li className="list-inline-item" key={s.item}><a href={s.item}>{s.name}</a></li>
    });

    const web = data.data.portfolio.webdesign.map((s) => {
      return <li className="list-inline-item" key={s.item}><a href={s.item}>{s.name}</a></li>
    });

    const soft = data.data.portfolio.project.map((s) => {
      return <li className="list-inline-item" key={s.item}><a href={s.item}>{s.name}</a></li>
    });

    return (
      <div className="card-content skills">
        <h4>Web Application</h4>
        <ul className="list-inline">{webapp}</ul>
        <h4>Web Design</h4>
        <ul className="list-inline">{web}</ul>
        <h4>Other Project</h4>
        <ul className="list-inline">{soft}</ul>
      </div>
    )
  }
}

function mapStateToProps({ReducerZonayed}) {
  return {ReducerZonayed}
}

export default connect(mapStateToProps)(Work);



